.rs-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    padding: 8px 16px;
    border: 1px solid;
    border-color: $primaryColor;
    background-color: $white;
    color: $primaryColor;
    font-size: rem(16);
    font-weight: 700;
    transition: 0.3s ease-in-out;
    letter-spacing: 0.16em;
    cursor: pointer;

    &:hover {
        box-shadow: 0 3px 5px 2px rgba($black, 0.1);
        // background-color: rgba($primaryColor, 1);
        transform: translateY(-1px);
        color: $primaryColor;
    }

    &.rs-btn-lg {
        padding: 12px 24px;
        font-size: rem(20);
    }

    &.rs-btn-contained {
        color: $primaryColor;
    }

    &.rs-btn-primary {
        background-color: $primaryColor;
        color: $white;
        // &:hover{
        //     background-color: darken($primaryColor, 30);
        // }
    }

    &.rs-btn-trans {
        border-color: transparent;
        background-color: transparent;
    }

    &.rs-btn-gray-bg {
        background-color: $dataTableSearchBgColor;
        border: none;
        color: $paymentInputColor3;
    }

    @media (max-width: 1448px) {
        font-size: rem(16px) !important;
        padding: 8px 16px !important;
    }

    @media (max-width: 1200px) {
        font-size: rem(14px) !important;
        padding: 8px 10px !important;
    }

    @media (max-width: 575px) {
        font-size: rem(10px) !important;
        padding: 8px 8px !important;
        letter-spacing: 1px;
    }
}

.ra-blance-btn {
    background-color: $tableRowColor;
}

.ra-cursor-pointer {
    cursor: pointer;
}

.ra-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    color: $primaryColor;
    font-size: rem(15px);
    font-weight: 500;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    border: none;

    &:hover {
        box-shadow: 0 3px 5px 2px rgba($black, 0.1);
        transform: translateY(-1px);
        color: $primaryColor;
    }

    &.ra-btn-dark-bg {
        background-color: $darkPrimaryColor;
        color: $white;
    }

    &.ra-btn-light-bg {
        background-color: $grayLight1;
        color: $black;
    }

    &.ra-btn-light-grey {
        background-color: $grayLight3;
        color: $black;
    }

    &.ra-btn-blue-bg {
        background-color: $darkBlueColor2;
        color: $white;
    }

    &.bg-green {
        background-color: $greenDark;
        color: $white;
    }

    &.ra-btn-6x12 {
        padding: 6px 12px;
    }

    &.ra-btn-8x16 {
        padding: 8px 16px;
    }

    &.ra-btn-8x11 {
        padding: 8px 11px;
    }

    &.ra-btn-10x20 {
        padding: 10px 20px;
    }

    &.ra-btn-border {
        border: 1px solid;
        border-color: $grayBorderColor;
    }

    &.ra-btn-border-dark {
        border: 1px solid;
        border-color: $grayLight1;
    }

    &.btn-12x12 {
        padding: 12px;
        border: 1px solid $grayLightBgColor;
        border-radius: 200px;
        height: 40px;
        width: 174px;
        display: flex;
        justify-content: center;
        align-items: center;
        letter-spacing: normal;
    }

    &.btn-6x16 {
        padding: 6px 16px;
        background-color: transparent;
        color: $black;

        &.active {
            background-color: $darkPrimaryColor;
            color: $white;
        }
    }

    &.blur {
        opacity: .6;
    }
}

.btn-tag {
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 6px !important;

    &.bg-grey {
        background-color: $grayProgressLight;
        color: $black;
    }

    &.bg-green {
        background-color: $greenDark;
        color: $white;
    }
}

.btn-4x12 {
    padding: 4px 12px;
}

.dropdown-menu {
    &.filter-dropdown {
        padding: 0px;
        margin-top: -7px;
        border: none;

        .dropdown-item {
            &:hover {
                background-color: $greenDark;
                color: $white;
            }
        }
    }
}